import * as API from '@/api/home'
import RecordList from '@/components/RecordList'
import Vue from 'vue'
export default {
  name: "personal-battle",
  data() {
      return {
          collapsed:{
            height:'1.5rem'
          },
          userId: "1",
          name: "",
          portrait: "/static/img/5f236c10dc4d2e83d386048aedf9e50c.jpg",
          honor: "",
          winRate: 0.8,
          selectTime: "all",
          filterOp: [
              {
                  id:"all",
                  content:"",
                  active:true,
              },
              {
                  id:"1",
                  content:"1",
                  active:false,
              },
              {
                  id:"3",
                  content:"3",
                  active:false,
              },
              {
                  id:"7",
                  content:"7",
                  active:false,
              }
          ],
          records: [
              {
                  founderName:"hhhhhhhhhhhhhhhhhh",
                  inviteeName:"userB",
                  founderPortrait:"/static/img/5f236c10dc4d2e83d386048aedf9e50c.jpg",
                  inviteePortrait:"/static/img/5f236c10dc4d2e83d386048aedf9e50c.jpg",
                  founderHonor:{url:"/static/icon-img/honor-.png",title:""},
                  inviteeHonor:"",
                  founderCardId:"",
                  inviteeCardId:"",
                  startTime:"2018-07-20",
                  endTime:"2018-07-25",
                  initialMoney:"",
                  founderBalance:"",
                  inviteeBalance:"",
                  founderRate:1.1,
                  inviteeRate:1.2,
              },
              {
                  founderName:"hhhhhhhhhhhhhhhhhh",
                  inviteeName:"userB",
                  founderPortrait:"/static/img/5f236c10dc4d2e83d386048aedf9e50c.jpg",
                  inviteePortrait:"/static/img/5f236c10dc4d2e83d386048aedf9e50c.jpg",
                  founderHonor:{url:"/static/icon-img/honor-.png",title:""},
                  inviteeHonor:"",
                  founderCardId:"",
                  inviteeCardId:"",
                  startTime:"2018-07-20",
                  endTime:"2018-07-29",
                  initialMoney:"",
                  founderBalance:"",
                  inviteeBalance:"",
                  founderRate:1.1,
                  inviteeRate:1.2,
              }
          ]
      }
  },
  created() {
      this.userId = this.$route.query.userId;
      this.getUserRecords(this.userId, "all");
  },
  methods:{
      back() {
          this.$router.go(-1);
      },
      tapFilter(id) {
          for(let i=0; i<this.filterOp.length; i++) {
              if(this.filterOp[i].id == id){
                  this.filterOp[i].active = true;
                  this.selectTime = id;
              }
              else
                  this.filterOp[i].active = false;
          }
          this.records = [];
          this.getUserRecords(this.userId, this.selectTime)
      },
      getUserRecords(userId, selectTime) {
        API.getPersonalBattle(userId, selectTime).then(res=>{
          this.name = res.name;
          this.portrait = res.portrait;
          this.honor = res.honor;
          this.winRate = res.winRate;
          this.records = res.records;
          if(this.records.length>0) {
              Vue.set(this.records[0],'collapsed',true);
          }
        })
      }
  },
  components:{
    RecordList
  },
}



