
import Popup from 'vux/src/components/popup/index.vue'

export default {
name:'popupTop',
props:{
isShow:{
type:Boolean,
default:false
},
autoClose:{
type:Boolean,
default:false
},
showClose:{
type:Boolean,
default:false
}

},
data(){
return {
value:false
}
},
components:{
Popup,

},

methods:{
handleHide(){
this.value = false
this.$emit('update:isShow',false)
}
}

}




